exports.push([module.id, "\n.caret .iconfont[_v-f750b48a]{\n  display: inline-block;\n  -webkit-transition: all .3s ease;\n  transition: all .3s ease;\n  -webkit-transform:rotate(0deg);\n          transform:rotate(0deg);\n}\n.caret.open .iconfont[_v-f750b48a]{\n  -webkit-transform:rotate(180deg);\n          transform:rotate(180deg);\n}\n", "", {"version":3,"sources":["/./src/components/Select/Select.vue?6b28f338"],"names":[],"mappings":";AAuNA;EACA,sBAAA;EACA,iCAAA;EAAA,yBAAA;EACA,+BAAA;UAAA,uBAAA;CACA;AACA;EACA,iCAAA;UAAA,yBAAA;CACA","file":"Select.vue","sourcesContent":["<template>\n  <div class=\"select-container\" v-bind:class=\"{open: show,disabled: disabled,multiple:multiple}\">\n    <div class=\"select-toggle\" tabindex=\"1\" class=\"dropdown-toggle\" @click=\"toggleDropdown\" v-bind=\"{disabled: disabled}\"\n    >\n      <template v-if=\"!multiple\">\n        <span class=\"select-placeholder\" v-show=\"showPlaceholder\">{{placeholder}}</span>\n        <span class=\"btn-content\">{{ showText }}</span>\n        <span :class=\"{caret:true,open:show}\"><icon type=\"down\" size=\"12\"></icon></span>\n      </template>\n      <div v-else>\n        <span class=\"select-placeholder\" v-show=\"showPlaceholder\">{{placeholder}}</span>\n        <tag v-for=\"option in selectedOptions\" closable @close=\"closeTag(option)\">{{{option.label}}}</tag>\n        <input type=\"text\" v-el:search-field class=\"select-search-field\" @input=\"onInput\" @keydown.delete=\"deleteTag\" @keydown.enter.prevent=\"createTag\" v-model=\"searchText\" autocomplete=\"off\"/>\n      </div>\n    </div>\n    <div class=\"dropdown-menu\">\n      <slot></slot>\n      <div v-show=\"noResult\" class=\"no-result\">无结果</div>\n      <div class=\"notify\" v-show=\"showNotify\" transition=\"fadein\">最多可选 ({{limit}})项.</div>\n    </div>\n  </div>\n</template>\n\n<script>\n  import EventListener from '../utils/EventListener'\n  import coerceBoolean from '../utils/coerceBoolean.js'\n  import Icon from '../Icon/'\n  import Tag from '../Tag/'\n  export default {\n    props: {\n      width:{\n        type: Array,\n      },\n      value: {\n        default:''\n      },\n      placeholder: {\n        type: String,\n        default: '请选择'\n      },\n      tags:{\n        type:Boolean\n      },\n      multiple: {\n        type: Boolean\n      },\n      search: { // Allow searching (only works when options are provided)\n        type: Boolean\n      },\n      limit: {\n        type: Number,\n        default: 1024\n      },\n      disabled: {\n        type: Boolean\n      }\n    },\n    components:{\n      Icon,\n      Tag\n    },\n    created() {\n      let me = this\n      if(me.tags) {\n        me.multiple = true\n      }\n      if(!me.value) {\n        me.value = me.multiple ? [] : ''\n      } \n      console.log(me.value)\n      if(me.multiple && !Array.isArray(me.value)) {\n        me.value = [me.value]\n      }\n      if(!me.multiple && Array.isArray(me.value)) {\n        me.value = me.value.slice(0, 1)\n      }\n      if (me.multiple && me.value.length > me.limit) {\n        me.value = me.value.slice(0, me.limit)\n      }\n      if(me.value.length) {\n        me.showPlaceholder = false\n      }\n    },\n    data() {\n      return {\n        searchText: '',\n        noResult:false,\n        show: false,\n        selectedOptions:[],\n        showPlaceholder:true,\n        showNotify: false,\n        options:[]\n      }\n    },\n    computed: {\n      showText() {\n        return this.selectedOptions && this.selectedOptions[0] && this.selectedOptions[0].label\n      }\n    },\n    watch: {\n      value(val) {\n        if (this.multiple && val.length > this.limit) {\n          this.showNotify = true\n          this.value.pop()\n          setTimeout(() => this.showNotify = false, 1000)\n        }\n      },\n      selectedOptions() {\n        if(this.multiple) {\n          this.value = this.selectedOptions.map((option)=>{\n            return option.value\n          })\n        } else {\n          this.value = this.selectedOptions[0].value\n        }\n      }\n    },\n    methods: {\n      select(option) {\n        this.$dispatch('change',option)\n      },\n      toggleDropdown() {\n        if(this.disabled) {\n          this.show = false\n          return\n        }\n        this.show = !this.show\n        if(this.multiple) {\n          this.showPlaceholder = false\n          this.$els.searchField.focus()\n        }\n      },\n      closeTag(option) {\n        this.selectedOptions.$remove(option)\n      },\n      deleteTag() {\n        let input = event.target\n        let value = input.value\n        if(value.length === 0) {\n          let options = this.selectedOptions\n          let option = options[options.length -1]\n          this.selectedOptions.$remove(option)\n        }\n      },\n      onInput() {\n        let input = event.target\n        let value = input.value\n        let width = value.length * 10\n        this.showPlaceholder = false\n        input.style.width = (width + 10) + 'px'\n      },\n      createTag() {\n        if(this.tags) {\n          let value = event.target.value\n          console.log(value)\n          if(!value || !value.trim().length) {\n            return\n          }\n          if(this.value.indexOf(value) === -1) {\n            this.selectedOptions.push({\n              label:value,\n              value:value\n            })\n          }\n          this.searchText = '';\n        }\n      }\n    },\n    events:{\n      change(option) {\n        this.showPlaceholder = false\n\n        if(this.multiple) {\n          let isSelected = this.selectedOptions.some((item)=>{\n            return item.value === option.value\n          })\n          if(!isSelected) {\n            this.selectedOptions.push(option)\n            this.value.push(option.value)\n          } else {\n            this.selectedOptions = this.selectedOptions.filter((item)=>{\n              return item.value !== option.value\n            })\n            console.log(this.value)\n            this.value.$remove(option.value)\n          }\n        } else {\n          this.selectedOptions = [option]\n          this.value = option.value\n        }\n\n        if (!this.multiple) {\n          this.show = false\n        }\n        this.searchText = ''\n        // 需要把option的change事件继续冒泡给上一层级调用\n        return true\n      }\n    },\n    ready() {\n      // 如果设置了子option元素同时又传了options，那么优先使用option子组件的内容\n      let me = this\n      me._closeEvent = EventListener.listen(window, 'click', (e)=> {\n        if (!me.$el.contains(e.target)) {\n          me.show = false\n        }\n      })\n    },\n    beforeDestroy() {\n      if (this._closeEvent) this._closeEvent.remove()\n    }\n  }\n</script>\n\n<style scoped>\n.caret .iconfont{\n  display: inline-block;\n  transition: all .3s ease;\n  transform:rotate(0deg);\n}\n.caret.open .iconfont{\n  transform:rotate(180deg);\n}\n</style>\n"],"sourceRoot":"webpack://"}]);

